.createPersonalEventAreaWrapper {
  .createEventArea {
    display: grid;
    grid-template-columns: 1fr;
    background: #ffffff;
    border: 1px solid #e5e5e5;
    box-sizing: border-box;
    box-shadow: 0px 4px 4px 5px rgba(234, 234, 234, 0.25);
    border-radius: 6px;
    .header {
      padding: 10px;
      display: flex;
      justify-content: space-between;
      span {
        font-family: ProximaNova-Regular;
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 16px;
        color: #010947;
      }
    }
    .hLine {
      height: 1px;
      border-bottom: 1px solid #e5e5e5;
    }
    .eventForm {
      .help-text {
        color: #010947;
      }
      .location {
        .form-check {
          display: inline;
          margin-right: 50px;
        }
      }
      .input-group-text {
        background-color: #e9ecfa;
        color: #000;
      }
    }
  }
}
